$darkest-blue: #000444;
$lighter-blue: #002644;

body {
    background: $darkest-blue;
    color: white;
}

.menubar {
    border: solid;
    border-color: white;
    text-align: right;
}

.landing {
    border: solid;
    border-color: red;
    display: flex;
    font-family: "Libre Baskerville", serif;
    font-weight: 400;
    font-style: normal;
    display: grid;
    grid-template-columns: 20vw 1fr;
    grid-template-rows: 100px 40vh;
    gap: 10px;
}

#logo {
    place-self: center;
    grid-column: 1;
    grid-row: 2
}

#logo img {
    max-width: 100%;
    max-height: 100%;
}

#summary {
    grid-column: 2;
    grid-row: 2;
    background-image: url("../images/Feather\ Effect.png");
    background-position-x: -50px;
    background-size: contain;
    background-repeat: repeat-x;
    align-content: center;
    padding-left: 10px;
}